import React, { Component } from 'react';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { bindActionCreators } from 'redux';
import { Spin, Tooltip, Icon, Col, Row, Card } from 'antd';
import Lightbox from 'react-image-lightbox';
import LazyLoad from 'react-lazyload';

import DocumentTitle from '../../component/document-title';
import ViewSourceCode from '../../component/view-source-code';

function img(name) {
  return `img/showcase/${name}`;
}

class Case extends Component {
  state = {
    shouldRend: false,
    lightboxImages: [],
    photoIndex: 0,
    isOpen: false,

    done: [
      {
        name: '',
        desc: (
          <div>
            <p>: React + Webpack</p>
            <p>wordpresshexoreact...</p>
          </div>
        ),
        screenshot: [1, 2, 3].map(i => img(`blog-${i}.png`)),
        homepage: `https://axetroy.github.io`
      },
      {
        name: '',
        desc: (
          <div>
            <p>: Angular1.x + Gulp + Webpack</p>
            <p>P2P/.</p>
          </div>
        ),
        screenshot: '',
        homepage: ``
      },
      {
        name: '',
        desc: (
          <div>
            <p>: Angular1.x + Gulp + Webpack</p>
            <p>/</p>
          </div>
        ),
        screenshot: [1].map(i => img(`gcb-wallet-${i}.png`)),
        homepage: ``
      },
      {
        name: '',
        desc: (
          <div>
            <p>: Angular1.x + Gulp + Webpack</p>
            <p>/</p>
          </div>
        ),
        screenshot: [1, 2, 3].map(i => img(`gcb-${i}.png`)),
        homepage: `http://gloriouspay.com/`
      },
      {
        name: 'K',
        desc: (
          <div>
            <p>: Angular1.x + Gulp + Webpack</p>
            <p>/</p>
          </div>
        ),
        screenshot: [1, 2].map(i => img(`kcoin-${i}.png`)),
        homepage: `http://kcoin.biz`
      },
      {
        name: '',
        desc: (
          <div>
            <p>: Angular1.x + Gulp + Webpack</p>
            <p>/</p>
          </div>
        ),
        screenshot: [1].map(i => img(`kongbow-${i}.png`)),
        homepage: `http://kongbow.com/`
      },
      {
        name: 'DOB',
        desc: (
          <div>
            <p>: Angular1.x + Gulp + Webpack</p>
            <p>/</p>
          </div>
        ),
        screenshot: [1, 2, 3].map(i => img(`dob-${i}.png`)),
        homepage: `http://www.db63.com/`
      },
      {
        name: '',
        desc: (
          <div>
            <p>: Angular1.x + Gulp + Webpack</p>
            <p>/</p>
          </div>
        ),
        screenshot: [1, 2].map(i => img(`woqi-${i}.png`)),
        homepage: ``
      },
      {
        name: 'KAO',
        desc: (
          <div>
            <p>: thinkPHP + requireJS</p>
            <p>KAO</p>
          </div>
        ),
        screenshot: [1, 2, 3].map(i => img(`kaopu-${i}.png`)),
        homepage: `http://cy.hydhmy.com/hyc/m/per.html`
      },
      {
        name: 'KAO',
        desc: (
          <div>
            <p>: Wepy</p>
            <p>/.</p>
          </div>
        ),
        screenshot: [1, 2, 3].map(i => img(`wxapp-kaopu-${i}.png`)),
        homepage: ``
      }
    ],
    undone: [
      {
        name: 'v3',
        desc: (
          <div>
            <p>: Angular2.x + Typescript + Webpack</p>
            <p>.</p>
          </div>
        ),
        screenshot: '',
        homepage: ``
      },
      {
        name: '',
        desc: (
          <div>
            <p>: Labarador</p>
            <p></p>
          </div>
        ),
        screenshot: [1, 2].map(i => img(`tuanjian-${i}.png`)),
        homepage: ``
      },
      {
        name: 'OAAPP',
        desc: (
          <div>
            <p>: /IOSwebviewwebreact + Webpack</p>
            <p>appandroid javaios object-c. webview</p>
          </div>
        ),
        screenshot: '',
        homepage: ``
      }
    ]
  };

  componentWillMount() {
    const location = this.props.location;
    if (
      window.__IS_ADMIN__ === true ||
      location.search.indexOf('viewer=admin') >= 0
    ) {
      window.__IS_ADMIN__ = true;
      this.setState({ shouldRend: true });
    } else {
      this.props.history.goBack();
    }
  }

  rendCase(title, cases) {
    const noScreenshotImg = 'img/no-img.jpg';
    return (
      <LazyLoad height={300} offset={100}>
        <div>
          <h2 style={{ textAlign: 'center' }}>{title}</h2>
          <Row gutter={16}>
            {cases.map(c => {
              return (
                <Col md={8} xs={24} key={c.name} style={{ margin: '1rem 0' }}>
                  <Card>
                    <div
                      style={{
                        position: 'relative',
                        overflow: 'hidden',
                        minHeight: '30rem'
                      }}
                    >
                      <div
                        style={{
                          backgroundImage: `url(${c.screenshot &&
                            c.screenshot.length
                            ? c.screenshot[0]
                            : noScreenshotImg})`,
                          backgroundSize: 'cover',
                          backgroundRepeat: 'no-repeat',
                          backgroundAttachment: 'interit',
                          verticalAlign: 'middle',
                          width: '100%',
                          height: '30rem'
                        }}
                        onClick={() =>
                          this.setState({
                            isOpen: true,
                            photoIndex: 0,
                            lightboxImages: c.screenshot && c.screenshot.length
                              ? c.screenshot
                              : [noScreenshotImg]
                          })}
                      />
                      <div
                        style={{
                          position: 'absolute',
                          bottom: 0,
                          left: 0,
                          backgroundColor: '#FAFAFA',
                          padding: '1rem',
                          width: '100%'
                        }}
                      >
                        <h3>
                          {c.homepage
                            ? <a href={c.homepage} target="_blank">{c.name}</a>
                            : c.name}
                        </h3>
                        <div>{c.desc}</div>
                      </div>
                    </div>
                  </Card>
                </Col>
              );
            })}
          </Row>
        </div>
      </LazyLoad>
    );
  }

  render() {
    let images = ['blog-1.png', 'blog-2.png', 'blog-3.png'].map(v => img(v));
    const { photoIndex, isOpen, lightboxImages, shouldRend } = this.state;
    return shouldRend
      ? <DocumentTitle title={['']}>
          <Spin spinning={false}>
            <div className="toolbar-container">
              <div className="edit-this-page">
                <Tooltip placement="topLeft" title="" arrowPointAtCenter>
                  <ViewSourceCode file="pages/case/index.js">
                    <a href="javascript: void 0" target="_blank">
                      <Icon
                        type="code"
                        style={{
                          fontSize: '3rem'
                        }}
                      />
                    </a>
                  </ViewSourceCode>
                </Tooltip>
              </div>
              {this.rendCase('', this.state.done)}
              {this.rendCase('', this.state.undone)}
              {isOpen
                ? <Lightbox
                    mainSrc={lightboxImages[photoIndex]}
                    nextSrc={
                      lightboxImages[(photoIndex + 1) % lightboxImages.length]
                    }
                    prevSrc={
                      lightboxImages[
                        (photoIndex + images.length - 1) % lightboxImages.length
                      ]
                    }
                    onCloseRequest={() =>
                      this.setState({ isOpen: false, photoIndex: 0 })}
                    onMovePrevRequest={() =>
                      this.setState({
                        photoIndex:
                          (photoIndex + lightboxImages.length - 1) %
                            lightboxImages.length
                      })}
                    onMoveNextRequest={() =>
                      this.setState({
                        photoIndex: (photoIndex + 1) % lightboxImages.length
                      })}
                  />
                : null}
            </div>
          </Spin>
        </DocumentTitle>
      : <div />;
  }
}
export default connect(
  function mapStateToProps(state) {
    return {};
  },
  function mapDispatchToProps(dispatch) {
    return bindActionCreators({}, dispatch);
  }
)(withRouter(Case));



